<template>
  <div class="content">
    {{error}}
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-size-100">  
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title"><i class="pi pi-user-plus" style="fontSize: 1rem"></i> 
            Register</h4>
            <p class="category">Complete el registro de Usuario</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Nombre</label>
                  <md-input v-model="nombre" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Apellido</label>
                  <md-input v-model="apellido" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Clave</label>
                  <md-input v-model="clave1" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Confirmar Clave</label>
                  <md-input v-model="clave2" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100">
                <div class="alert alert-success">
                  <span>
                    La clave debe tener una longitud mínima de 8, mayúsculas, minísculas y un carácter especial ! @ # $ % ^ & * _ . 
                  </span>
                </div>
              </div>
              <div class="md-layout-item md-size-100 text-right">
                <md-button class="md-raised md-success" @click.native.prevent="Registrar">
                  Procesar</md-button>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>
    </div>
  </div>
</template>
<script>
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      estatus:  false,
      correo:   "",
      clave1:   "",
      clave2:   "",
      nombre:   "",
      apellido:   "",
      error:    null,
      validar:  false,
      mens:     null
    };
  },
  created () {
    API({
      action:  'estatus'
    }).then(response => {
      this.estatus = response.data.estatus;
    }).catch(e => {console.log(e);})
},
  methods: {
    Registrar() {
      if (this.nombre==null || this.apellido==null ||  this.correo==null 
          || this.clave1==null || this.clave2==null) {
        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.correo)){
        this.$notify(Mensaje(3,'El correo no es válido'));
      } else if (!(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[_.!@#\$%\^&\*])(?=.{8,})/).test(this.clave1)){
        this.$notify(Mensaje(3,'La clave debe tener una longitud mínima de 8, mayúsculas, minísculas y un carácter especial'));  
      } else if (this.clave1 != this.clave2){
        this.$notify(Mensaje(3,'La confirmación de clave es incorrecta')); 
      } else {
          this.validar = true;
      }
      if (this.validar) {
        API({
          action:  'registrar',
          nombre:   this.nombre,
          apellido: this.apellido,
          correo:   this.correo,
          clave: Crypto.HmacSHA1(this.clave1, "btcoin").toString()
          }).then(response => {
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(0,response.data.message));
            }   
          }).catch(e => {console.log(e);});
          this.clave1 = '';
          this.clave2 = '';
      }
    }
  }
} 
</script>