<template>
  <div class="content">
    {{error}}
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-20 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="green">
          <template slot="header">
            <md-icon>assessment</md-icon>
          </template>
          <template slot="content">  
            <br>          
            <h3 class="title">{{reporte.inversion_total}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Total
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-20 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="blue">
          <template slot="header">
            <md-icon>trending_up</md-icon>
          </template>
          <template slot="content">
            <br>          
            <h3 class="title">{{reporte.inversion_ejecucion}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              En Ejecución
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-20 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="grey">
          <template slot="header">
            <md-icon>done_all</md-icon>
          </template>
          <template slot="content">
            <br>          
            <h3 class="title">{{reporte.inversion_completada}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Completadas
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-20 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="red">
          <template slot="header">
            <md-icon>lock</md-icon>
          </template>
          <template slot="content">
            <br>          
            <h3 class="title">{{reporte.inversion_cerrada}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cerradas
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-20 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="orange">
          <template slot="header">
            <md-icon>thumb_up</md-icon>
          </template>
          <template slot="content">
            <br>          
            <h3 class="title">{{reporte.inversionistas}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Inversionistas
            </div>
          </template>
        </stats-card>
      </div>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="!modificar.activo">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Portafolio de Inversiones</h4>
          <p class="category">Total: {{portafolio.length}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="portafolio" >
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N°">{{ index+1 }}</md-table-cell>
              <md-table-cell md-label="Nombre">{{ item.titulo }}</md-table-cell>
              <md-table-cell md-label="Objetivo">${{ parseFloat(item.meta).toFixed(2) }}</md-table-cell>
              <md-table-cell md-label="Recaudado">
                  ${{ parseFloat(item.monto).toFixed(2) }} ({{Math.round((item.monto/item.meta)*100)}}%)
              </md-table-cell>
              <md-table-cell md-label="Inversionistas">{{ item.invers }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha_ini }} / {{ item.fecha_fin }}</md-table-cell>
              <md-table-cell md-label="Estatus">{{ etapa[item.etapa] }}</md-table-cell>
              <md-table-cell md-label="Editar">
                <md-list-item @click="Editar(index)">
                  <i class="material-icons">edit</i>
                </md-list-item>
              </md-table-cell>
            </md-table-row>
          </md-table>           
            <md-list-item @click="Editar()">
              <div class="stats">
                <md-icon>add_circle_outline</md-icon>Nuevo
              </div>
            </md-list-item>
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout" v-else>
      <div class="md-layout-item md-medium-size-100 md-size-100">  
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Nuevo/Modificar Portafolio</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-70">
                <md-field>
                  <label>Tipo de Trading</label>
                  <md-input v-model="modificar.nombre" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30">
                <md-field>
                  <label>Objetivo de Recaudación</label>
                  <md-input v-model="modificar.objetivo" type="number" step="0.01" min="0"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>Fecha Inicio</label>
                  <md-input v-model="modificar.fecha_ini" type="date"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>Fecha Fin</label>
                  <md-input v-model="modificar.fecha_fin" type="date"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-20">
                <md-field>
                  <label>Múltiplo de Monto $</label>
                  <md-input v-model="modificar.multiplo" type="number" step="1" min="0"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-40">
                <md-field>
                  <label>Estatus</label>
                  <md-select v-model="modificar.etapa" >
                    <md-option v-for="(item, index) in etapa" :key="index" :value="index">
                        {{item}}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Descripción</label>
                  <md-input v-model="modificar.descripcion" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-70 text-right"></div>
              <div class="md-layout-item md-size-15 text-right">
                <md-list-item @click="Guardar()">
                  <div class="stats">
                    <md-icon>save</md-icon>Guardar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-15 text-right">
                <md-list-item @click="Cerrar()">
                  <div class="stats">
                    <md-icon>close</md-icon>Cerrar
                  </div>
                </md-list-item>
              </div>

            </div>
          </md-card-content>
        </md-card>   
      </div>
    </div>
    
  </div>
</template>

<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error: null,
      reporte: [],
      portafolio: [],
      etapa: ['En Proceso','Completo','Cerrado'],
       modificar: {
        activo: false,
        id: 0,
        nombre: null,
        descripcion: null,
        fecha_ini: null,
        fecha_fin: null,
        estatus: null,
        objetivo: 0,
        etapa: 0,
        multiplo: 0
      },
    }
  },
  created () { 
    Security(2);
    this.Mostrar();    
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.reporte =[];
      this.portafolio =[];
      API({
        action:  'dashboard',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.reporte=response.data;
      });
      API({
        action:  'portafolio',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
          this.portafolio=response.data.portafolio;
      });
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.portafolio[index].id;
      this.modificar.nombre = this.portafolio[index].titulo;
      this.modificar.descripcion = this.portafolio[index].descripcion;
      this.modificar.fecha_ini = this.portafolio[index].fecha_ini;
      this.modificar.fecha_fin = this.portafolio[index].fecha_fin;
      this.modificar. estatus = this.portafolio[index].estatus;
      this.modificar.objetivo = parseFloat(this.portafolio[index].meta).toFixed(2);
      this.modificar.etapa = this.portafolio[index].etapa;
      this.modificar.multiplo = this.portafolio[index].multiplo;
      this.Mostrar();
    },
    Guardar() {
      API({
        action:  'updateportafolio',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        id: this.modificar.id,
        nombre: this.modificar.nombre,
        descripcion: this.modificar.descripcion,
        fecha_ini: this.modificar.fecha_ini,
        fecha_fin: this.modificar.fecha_fin,
        meta: this.modificar.objetivo,
        etapa:  this.modificar.etapa,
        multiplo:  this.modificar.multiplo,
      }).then(response => {
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(0,response.data.message));
          this.portafolio = [];
          this.Mostrar();
        }   
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.nombre = null;
      this.modificar.descripcion = null;
      this.modificar.fecha_ini = null;
      this.modificar.fecha_fin = null;
      this.modificar.estatus = null;
      this.modificar.objetivo = 0;
      this.modificar.etapa = 0;
    }



    
  }
};
</script>
