<script>
import API from "./GlobalAPI";
import InactiveJS from 'inactivejs';

export default {
  data() {
    return {
      estatus:      false,
      rol:          0,
      autenticado:  false,
      user:         null,
      timer: '',
      selectedEmployee: null,
      employees: [
        "Jim Halpert",
        "Dwight Schrute",
        "Michael Scott",
        "Pam Beesly",
        "Angela Martin",
        "Kelly Kapoor",
        "Ryan Howard",
        "Kevin Malone"
      ]
    };
  },
  created () {
    if(!localStorage.getItem('Rol')>0 && localStorage.getItem('Verificacion')){
      this.Logout();
    }
    this.autenticado = localStorage.getItem('Verificacion');
    API({
      action:  'estatus'
    }).then(response => {
      this.estatus = response.data.estatus;
    }).catch(e => {console.log(e);})
    API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
    }).then(response => {
        this.user = response.data.user.nombre + ' ' + response.data.user.apellido;
    });
    if (localStorage.getItem('Rol')>0){
      const onAway = () => {
        this.Logout();
      };
      const inactiveInstance = new InactiveJS({
        timeout: 30 *60*1000, //3 min
        onAway: onAway,
      });
    }
},
  methods: {
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    Logout() {
      localStorage.removeItem('Verificacion');
      localStorage.removeItem('Rol');
      localStorage.removeItem('Login');
      localStorage.removeItem('Password');
      window.location.href = '#/inicio';
      this.$router.go(0);
    },

  }
};
</script>

