<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard, ChartCard } from "@/components";

export default {
  components: {
    StatsCard,
    ChartCard
  },

  data() {
    return {
      error: null,
      wallet: null,
      reporte: [],
      dashboard: [],
      saldo: 0,
      balance: 0,
      mercado: 0,
      admin: false,
      ver: 0,
      detalle_inversiones: [],
      mes: ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul","Ago", "Sep", "Oct", "Nov", "Dic"],
      options: {},
      color: ['#26c6da','red','orange','purple','green','gray'],
      usuariosChart: {
          labels: [],
          series: []
      },
      portafolioChart: {
          labels: [],
          series: []
      },
      intercambioChart: {
          labels: [],
          series: []
      },
      intercambio2Chart: {
          labels: [],
          series: []
      },
      inversionesChart: {
          labels: [],
          series: [],
          plan: []
      },
      estdinverChart: {
        data: {
          labels: [],
          series: []
        },
        options: {
          lineSmooth: this.$Chartist.Interpolation.cardinal({
            tension: 0
          }),
          low: 0,
          high: 100000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 0,
            bottom: 0,
            left: 20
          }
        }
      },



      dailySalesChart: {
        data: {
          labels: ["Ene", "Feb", "Mar", "Abr", "May", "Jun", "Jul","Ago", "Sep", "Oct", "Nov", "Dic"],
          series: [[12, 17, 7, 17, 23, 18, 38, 17, 23, 18, 38, 45]]
        },
        options: {
          lineSmooth: this.$Chartist.Interpolation.cardinal({
            tension: 0
          }),
          low: 0,
          high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
          chartPadding: {
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
          }
        }
      },

    }
  },
  created () { 
    Security(1);
    if (localStorage.getItem('Rol')==2){
      this.admin=true;
    } else {
      this.admin=false;
    }
    this.Mostrar();   
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.saldo = 0;
      this.balance = 0;
      this.mercado = 0;
      this.wallet =  null;
      this.ver = 0;
      this.detalle_inversiones= [];
      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.reporte=response.data;
        const user = response.data.user;
        if (this.admin) {
          API({
            action:  'dashboard',
            correo:   localStorage.getItem('Login'),
            clave:    localStorage.getItem('Password')
          }).then(response => {
            this.dashboard=response.data;   
            this.detalle_inversiones=response.data.detalle_inversiones;

            this.portafolioChart.labels.push((response.data.inversion_ejecucion*100/response.data.inversion_total).toFixed(0)+'%');
            this.portafolioChart.series.push(response.data.inversion_ejecucion);
            this.portafolioChart.labels.push((response.data.inversion_cerrada*100/response.data.inversion_total).toFixed(0)+'%');
            this.portafolioChart.series.push(response.data.inversion_cerrada);
            this.portafolioChart.labels.push((response.data.inversion_completada*100/response.data.inversion_total).toFixed(0)+'%');
            this.portafolioChart.series.push(response.data.inversion_completada); 

            this.usuariosChart.labels.push((response.data.user_admin*100/response.data.user_total).toFixed(0)+'%');
            this.usuariosChart.series.push(response.data.user_admin);
            this.usuariosChart.labels.push((response.data.user_inactivo*100/response.data.user_total).toFixed(0)+'%');
            this.usuariosChart.series.push(response.data.user_inactivo);
            this.usuariosChart.labels.push((response.data.user_basico*100/response.data.user_total).toFixed(0)+'%');
            this.usuariosChart.series.push(response.data.user_basico); 

            this.intercambioChart.labels.push((response.data.interc_USDUSD[0].operaciones*100/response.data.interc_TOTAL).toFixed(0)+'%');
            this.intercambioChart.series.push(response.data.interc_USDUSD[0].operaciones);
            this.intercambioChart.labels.push((response.data.interc_USDBTC[0].operaciones*100/response.data.interc_TOTAL).toFixed(0)+'%');
            this.intercambioChart.series.push(response.data.interc_USDBTC[0].operaciones);

            this.intercambio2Chart.labels.push('$'+parseFloat(response.data.interc_USDUSD[0].monto).toFixed(2));
            this.intercambio2Chart.series.push(response.data.interc_USDUSD[0].monto);
            this.intercambio2Chart.labels.push('$'+parseFloat(response.data.interc_USDBTC[0].monto).toFixed(2));
            this.intercambio2Chart.series.push(response.data.interc_USDBTC[0].monto);

            this.estdinverChart.data.labels=response.data.interc_estad.reverse();
            this.estdinverChart.data.series.push(response.data.interc_estadUSDUSD.reverse());
            this.estdinverChart.data.series.push(response.data.interc_estadUSDBTC.reverse());

            const max1 = Math.max.apply(null, response.data.interc_estadUSDUSD);
            const max2 = Math.max.apply(null, response.data.interc_estadUSDBTC);
            this.estdinverChart.options.high = Math.max(max1,max2);

          });
        } else {
          response.data.mis_inversiones.forEach(element => {
            this.inversionesChart.labels.push('$'+parseFloat(element.total).toFixed(0));
            this.inversionesChart.series.push(element.total);
            this.inversionesChart.plan.push(element.titulo);
          });
        }
       });


    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    },

  }
};
</script>
