<template>
  <div class="content">
  {{error}}
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="orange">
          <template slot="header">
            <div style="width:60px;" >
              <IconCrypto coinname="BTC" color="black" format="svg"/>
            </div>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{mercado}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cotización de Intercambio
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="orange">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">Saldo</p>
            <h3 class="title">${{saldo}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Valor en BTC {{(saldo/mercado).toFixed(8)}}
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="blue">
          <template slot="header">
            <div style="width:60px;" >
              <IconCrypto coinname="BTC" color="white" format="svg"/>
            </div>
          </template>
          <template slot="content">
            <p class="category">BTC</p>
            <h3 class="title">{{balance}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Saldo de Mi Wallet: ${{(balance*mercado).toFixed(2)}}
            </div>
          </template>
        </stats-card>
      </div>
    </div>

    <div class="md-layout">
      <div class="md-layout-item md-medium-size-66 md-size-66">  
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Mi Wallet BTC</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Address</label>
                  <md-input v-model="wallet" type="text" 
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Private Key (Wallet Import Format)</label>
                  <md-input v-model="WIF" type="password"
                  :disabled="disabled == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-30">
                <md-list-item @click="Ver(2)">
                  <div class="stats">
                    <md-icon>visibility</md-icon>Operaciones
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled"></div>
              <div class="md-layout-item md-size-20 text-right" v-if="disabled">
                <md-list-item @click="Editar()">
                  <div class="stats">
                    <md-icon>edit</md-icon>Editar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-25 text-right" v-if="!disabled">
                <md-list-item @click="Guardar()">
                  <div class="stats">
                    <md-icon>save</md-icon>Guardar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-25 text-right" v-if="!disabled">
                <md-list-item @click="Generar()">
                  <div class="stats">
                    <md-icon>published_with_changes</md-icon>Generar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-20 text-right" v-if="!disabled">
                <md-list-item @click="Cerrar()">
                  <div class="stats">
                    <md-icon>close</md-icon>Cerrar
                  </div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>

      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-33">
        <stats-card data-background-color="blue">
          <template slot="header">
            <md-icon>qr_code_2</md-icon>
          </template>
          <template slot="content">
            <p class="category">Código QR</p>
            <img style="width:165px;" :src="qr">
          </template>
          <template slot="footer">
            <div class="stats">
              <md-icon>qr_code_2</md-icon>Mi Wallet
            </div>
          </template>
        </stats-card>
      </div>



    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 1">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Utxos</h4>
          <p class="category">Satoshis: {{utxos_disp}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="utxos" >
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Hash">{{ item.txid }}</md-table-cell>
              <md-table-cell md-label="Valor">{{ item.satoshis }}</md-table-cell>
              <md-table-cell md-label="Confirmed">{{ item.confirmed }}</md-table-cell>
            </md-table-row>
          </md-table>           
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="ver == 2">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Operaciones</h4>
          <p class="category">Cantidad de operaciones: {{movimientos.length}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="movimientos" >
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="TxID">{{ item.hash }}</md-table-cell>
              <md-table-cell md-label="Credito">{{ item.credito }}</md-table-cell>
              <md-table-cell md-label="Debito">{{ item.debito }}</md-table-cell>
              <md-table-cell md-label="Estado">{{ item.confirmed }}</md-table-cell>
            </md-table-row>
          </md-table>           
        </md-card-content>
      </md-card>
    </div>

  </div>
</template>

<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');
import QR from "qr-encode";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error:    null,
      wallet: null,
      qr: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      disabled: true,
      valor: null,
      ver: 0,
      transactions: 0,
      utxos: [],
      utxos_disp: 0,
      movimientos: [],

    };
  },
  created () { 
    Security(1);
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.WIF =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = [];
      this.utxos = [];
      this.movimientos = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;

      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        const user = response.data.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.saldo = parseFloat(response.data.saldo).toFixed(2);
        const porcentaje = parseFloat(response.data.configuracion.price_spread);
        var dataURI = QR(this.wallet, {type: 6, size: 5, level: 'Q'});
        var img = new Image();
        img.src = dataURI;
        this.qr = img.src;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+porcentaje))/100).toFixed(2);
        });
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.getUtxo(this.wallet).then(result => {
          this.utxos = result;
          let disp = 0;
          for (let i = 0; i < result.length; i++) {
            if (result[i]['confirmed']) {
              disp += result[i]['satoshis'];
            }
          }
          this.utxos_disp = disp;
        });
        Bitcoin.getTxs(this.wallet).then(result => {
          result.forEach(element => {
            let salida = 0;
            for(let i = 0; i < element['vin'].length; i++){
              if (element['vin'][i]['prevout']['scriptpubkey_address']==this.wallet){
                salida = salida + element['vin'][i]['prevout']['value']
              }
            }
            let entrada = 0;
            for(let i = 0; i < element['vout'].length; i++){
              if (element['vout'][i]['scriptpubkey_address']==this.wallet){
                entrada = entrada + element['vout'][i]['value']
              }
            }
            let credito = 0;
            let debito = 0;
            if (salida>0){
              debito = ((salida - entrada)/100000000).toFixed(8) + ' BTC';
            } else {
              credito = (entrada/100000000).toFixed(8) + ' BTC';
            }


            let value = 0;
            for(let i = 0; i < element['vout'].length; i++){
              value = value + element['vout'][i]['value'];
            }
            let estado = 'Procesando';
            if (element["status"]["confirmed"]){
              estado = 'Procesada';
            }
            this.movimientos.push({
              hash: element['txid'],
              satoshis: (value/100000000).toFixed(8) + ' BTC',
              confirmed: estado,
              credito: credito,
              debito: debito,
            });
          });
        });
      });
    },
    Editar() {
      this.disabled = false;
    },
    Guardar() {
      if (this.WIF != '' && Bitcoin.isValidWIF(this.WIF)){
        Bitcoin.privateKeyToAccount(this.WIF);
        var validar = Bitcoin.defaultAccount.address;
      } else {
        var validar = this.wallet;
      }
      if (!Bitcoin.isValidAddress(this.wallet)){
        this.$notify(Mensaje(3,'Address No es Inválida.'));
      } else if (this.WIF != '' && !Bitcoin.isValidWIF(this.WIF)){
        this.$notify(Mensaje(3,'Private Key es Inválida'));
      } else if (this.WIF != '' && validar != this.wallet){
        this.$notify(Mensaje(3,'Private Key para la Address es Inválida'));
      } else {
          const securewif = secure.encrypt(this.WIF, 'btcoin');
          API({
            action:  'updatewallet',
            correo:   localStorage.getItem('Login'),
            clave:    localStorage.getItem('Password'),
            wallet: this.wallet,
            wif: securewif
          }).then(response => {
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(2,response.data.message));
              this.type = "password";
              this.disabled = true;
              this.Mostrar();
            }   
          });
      }
    },
    Cerrar() {
      this.disabled = true;
      this.Mostrar();
    },
    Generar() {
      this.type = "text";
      //const ck = new CoinKey.createRandom();
      //TESNET
      const ck = CoinKey.createRandom(ci('BTC-TEST'))
      this.wallet = ck.publicAddress;
      this.WIF = ck.privateWif;
      //this.Hex = ck.privateKey.toString('hex');
    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    },
  }
};
</script>