<template>
  <div class="content">
  {{error}}

    <div class="md-layout">
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="green">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{mercado}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
