            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="orange">
          <template slot="header">
            <md-icon>attach_money</md-icon>
          </template>
          <template slot="content">
            <p class="category">1 BTC</p>
            <h3 class="title">${{((mercado*(100+parseFloat(porcentaje)))/100).toFixed(2)}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Valor BTC + {{porcentaje}}%
            </div>
          </template>
        </stats-card>
      </div>
      <div class="md-layout-item md-medium-size-33 md-xsmall-size-100 md-size-100">
        <stats-card data-background-color="blue">
          <template slot="header">
            <div style="width:60px;" >
              <IconCrypto coinname="BTC" color="white" format="svg"/>
            </div>
          </template>
          <template slot="content">
            <p class="category">Cuenta Maestra BTC</p>
            <h3 class="title">{{balance}}</h3>
          </template>
          <template slot="footer">
            <div class="stats">
              Cuenta Maestra ${{(balance*((mercado*(100+parseFloat(porcentaje)))/100)).toFixed(2)}}
            </div>
          </template>
        </stats-card>
      </div>

      <div class="md-layout-item md-medium-size-40 md-size-100">  
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Configuración de Wallet</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Producto y Servicios: Address</label>
                  <md-input v-model="address.servicio" type="text" 
                  :disabled="disabled0 == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Comisión de Uso: Address</label>
                  <md-input v-model="address.comision" type="text"
                  :disabled="disabled0 == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Proveedor de Liquidez: Address</label>
                  <md-input v-model="address.proveedor" type="text"
                  disabled></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled0"></div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled0">
                <md-list-item @click="Editar(0)">
                  <div class="stats">
                    <md-icon>edit</md-icon>Editar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled0">
                <md-list-item @click="Guardar(0)">
                  <div class="stats">
                    <md-icon>save</md-icon>Guardar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled0">
                <md-list-item @click="Cerrar(0)">
                  <div class="stats">
                    <md-icon>close</md-icon>Cerrar
                  </div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>

      <div class="md-layout-item md-medium-size-30 md-size-100">  
        <md-card>
          <md-card-header data-background-color="orange">
            <h4 class="title">Comisiones</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>% Comisión de Uso</label>
                  <md-input v-model="comision" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled1 == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>% Comisión Spread Price</label>
                  <md-input v-model="porcentaje" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled1 == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>% Comisión de Retiro Anticipado</label>
                  <md-input v-model="retiro" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled1 == true"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled1"></div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled1">
                <md-list-item @click="Editar(1)">
                  <div class="stats">
                    <md-icon>edit</md-icon>
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled1">
                <md-list-item @click="Guardar(1)">
                  <div class="stats">
                    <md-icon>save</md-icon>
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled1">
                <md-list-item @click="Cerrar(1)">
                  <div class="stats">
                    <md-icon>close</md-icon>
                  </div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>

      <div class="md-layout-item md-medium-size-30 md-size-100">  
        <md-card>
          <md-card-header data-background-color="green">
            <h4 class="title">Porcentajes</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">


              <div class="md-layout-item md-small-size-100 md-size-100"
              v-for="(stats, index) in portafolio" :key="index">
                <md-field>
                  <label>% {{stats.titulo}}</label>
                  <md-input v-model="portafolio[index].porcentaje_inver" type="number" step="0.01"
                  min="0" max="100" :disabled="disabled2 == true"></md-input>
                </md-field>
              </div>



              <div class="md-layout-item md-size-50 text-right" v-if="disabled2"></div>
              <div class="md-layout-item md-size-50 text-right" v-if="disabled2">
                <md-list-item @click="Editar(2)">
                  <div class="stats">
                    <md-icon>edit</md-icon>
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled2">
                <md-list-item @click="Guardar(2)">
                  <div class="stats">
                    <md-icon>save</md-icon>
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-50 text-right" v-if="!disabled2">
                <md-list-item @click="Cerrar(2)">
                  <div class="stats">
                    <md-icon>close</md-icon>
                  </div>
                </md-list-item>
              </div>
            </div>
          </md-card-content>
        </md-card>   
      </div>





    </div>






  </div>
</template>

<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error:    null,
      mercado: 0,
      porcentaje: 0,
      comision: 0,
      retiro: 0,
      wallet: null,
      balance: 0,
      WIF: null,
      address: {
        proveedor:  "-",
        comision:   "-",
        servicio:   "-"
      },
      Hex: null,
      disabled0: true,
      disabled1: true,
      disabled2: true,
      hash: null,
      valor: null,
      transactions: 0,
      ver: 0,
      portafolio: [],

    };
  },
  created () { 
    Security(2);
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.porcentaje =  0;
      this.comision =  0;
      this.retiro =  0;
      this.WIF =  null;
      this.address =  {};
      this.Hex =  null;
      this.disabled0 =  true;
      this.disabled1 =  true;
      this.disabled2 =  true;
      this.hash = null;
      this.valor = null;
      this.portafolio =[];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      API({
        action:  'configuracion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.porcentaje = response.data.configuracion.price_spread;
        this.comision = response.data.configuracion.comision;
        this.retiro = response.data.configuracion.retiro_antic;
        const decWIF = secure.decrypt(response.data.configuracion.proveedor_wif, 'btcoin');
        this.WIF = decWIF;
        this.address.proveedor = response.data.configuracion.proveedor_address;
        this.address.comision = response.data.configuracion.comision_address;
        this.address.servicio = response.data.configuracion.servicio_address;
        Bitcoin.privateKeyToAccount(decWIF);
        this.wallet = Bitcoin.defaultAccount.address;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', '')).toFixed(2)
          this.mercado  = btc;
        });
        Bitcoin.getBalance().then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        API({
          action:  'portafolio',
          correo:   localStorage.getItem('Login'),
          clave:    localStorage.getItem('Password')
        }).then(response => {
          this.portafolio=response.data.portafolio;
        });


      });
    },
    Editar(caso) {
      if (caso == 0){this.disabled0 = false;}
      if (caso == 1){this.disabled1 = false;}
      if (caso == 2){this.disabled2 = false;}      
    },
    Guardar(caso) {
      if (!Bitcoin.isValidAddress(this.address.servicio)){
        this.$notify(Mensaje(3,'Address de Servicio Inválida.'));
      } else if (!Bitcoin.isValidAddress(this.address.comision)){
        this.$notify(Mensaje(3,'Address de Servicio Inválida.'));
      } else if (!Bitcoin.isValidAddress(this.address.proveedor)){
        this.$notify(Mensaje(3,'Address de Proveedor Inválida.'));
      } else if (this.address.proveedor != this.wallet){
        this.$notify(Mensaje(3,'Private Key de Proveedor Inválida'));
      } else {

        if (caso == 2){
          for (let i = 0; i < this.portafolio.length; i++) {
            API({
              action:  'updateportafolio_porc',
              correo:   localStorage.getItem('Login'),
              clave:    localStorage.getItem('Password'),
              id: this.portafolio[i].id,
              porcentaje_inver:  this.portafolio[i].porcentaje_inver,
            }).then(response => {
              if (response.data.error){
                this.$notify(Mensaje(3,response.data.message));
              } else {
                this.$notify(Mensaje(0,response.data.message));
                this.portafolio = [];
                this.Mostrar();
              }   
            });
          }
        } else {
          API({
            action:  'updateconfiguracion',
            correo:   localStorage.getItem('Login'),
            clave:    localStorage.getItem('Password'),
            price_spread: this.porcentaje,
            comision: this.comision,
            retiro_antic: this.retiro,
            proveedor_address: this.address.proveedor,
            proveedor_wif: secure.encrypt(this.WIF, 'btcoin'),
            servicio_address: this.address.servicio,
            comision_address: this.address.comision
          }).then(response => {
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
              this.Mostrar();
            } else {
              this.$notify(Mensaje(0,response.data.message));
            }   
          });

        }
        if (caso == 0){this.disabled0 = true;}
        if (caso == 1){this.disabled1 = true;}
        if (caso == 2){this.disabled2 = true;}      
      }

    },
    Cerrar(caso) {
      if (caso == 0){this.disabled0 = true;}
      if (caso == 1){this.disabled1 = true;}
      if (caso == 2){this.disabled2 = true;}      
    },
  }
};
</script>