<template>
  <div class="content">
    {{error}}

    <div class="md-layout-item md-medium-size-100 md-size-100" v-if="!modificar.activo">   
      <md-card>
        <md-card-header data-background-color="orange">
          <h4 class="title">Usuarios Registrados</h4>
          <p class="category">Total de Usuario: {{usuarios.length}}</p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="usuarios" >
            <md-table-row slot="md-table-row" slot-scope="{ item, index }">
              <md-table-cell md-label="N°">{{index+1}}</md-table-cell>
              <md-table-cell md-label="Apellido">{{ item.apellido }}</md-table-cell>
              <md-table-cell md-label="Nombre">{{ item.nombre }}</md-table-cell>
              <md-table-cell md-label="Correo">{{ item.correo }}</md-table-cell>
              <md-table-cell md-label="Rol">{{ roles[item.rol] }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Editar">
                <md-list-item @click="Editar(index)">
                  <i class="material-icons">edit</i>
                </md-list-item>
              </md-table-cell>
            </md-table-row>
          </md-table>           
        </md-card-content>
      </md-card>
    </div>

    <div class="md-layout" v-else>
      <div class="md-layout-item md-medium-size-100 md-size-100">  
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">Modificar Usuario</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">

              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Apellido</label>
                  <md-input v-model="modificar.apellido" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Nombre</label>
                  <md-input v-model="modificar.nombre" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="modificar.correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Rol</label>
                  <md-select v-model="modificar.rol" >
                    <md-option v-for="(item, index) in roles" :key="index" :value="index">
                        {{item}}
                    </md-option>
                  </md-select>
                </md-field>
              </div>
              <div class="md-layout-item md-size-70 text-right"></div>
              <div class="md-layout-item md-size-15 text-right">
                <md-list-item @click="Guardar()">
                  <div class="stats">
                    <md-icon>save</md-icon>Guardar
                  </div>
                </md-list-item>
              </div>
              <div class="md-layout-item md-size-15 text-right">
                <md-list-item @click="Cerrar()">
                  <div class="stats">
                    <md-icon>close</md-icon>Cerrar
                  </div>
                </md-list-item>
              </div>


            </div>
          </md-card-content>
        </md-card>   
      </div>
    </div>


      
  </div>
</template>

<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";

export default {

  data() {
    return {
      error: null,
      indice: 0,
      usuarios: [],
      modificar: {
        activo: false,
        id: 0,
        apellido: null,
        nombre: null,
        correo: null,
        rol: null
      },
      roles: ['Inactivo','Básico','Administrador']
    }
  },
  created () { 
    Security(2);
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.indice = 0;
      API({
        action:  'usuarios',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        this.usuarios=response.data.usuario;
      });
    },
    Editar(index) {
      this.modificar.activo = true;
      this.modificar.id = this.usuarios[index].id;
      this.modificar.apellido = this.usuarios[index].apellido;
      this.modificar.nombre = this.usuarios[index].nombre;
      this.modificar.correo = this.usuarios[index].correo;
      this.modificar.rol = this.usuarios[index].rol;
    },
    Guardar() {
      API({
        action:  'updateusuario',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        id: this.modificar.id,
        apellido: this.modificar.apellido,
        nombre: this.modificar.nombre,
        correo2: this.modificar.correo,
        rol: this.modificar.rol
      }).then(response => {
        //this.error = response.data;
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
        } else {
          this.$notify(Mensaje(0,response.data.message));
          this.uusarios = [];
          this.Mostrar();
          this.modificar.activo = false;
        }   
      });
    },
    Cerrar() {
      this.modificar.activo = false;
      this.modificar.id = 0;
      this.modificar.apellido = null;
      this.modificar.nombre = null;
      this.modificar.correo = null;
      this.modificar.gestor = 0;
      this.modificar.admin = 0;
    }



    
  }
};
</script>
