<script>
import Security from "./Layout/Security";
import API from "./Layout/GlobalAPI";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT('testnet');

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error:    null,
      wallet: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      disabled: true,
      valor: null,
      vertrans: false,
      verutxo: false,
      transactions: 0,
      utxos: [],
      utxos_disp: 0,
      movimientos: [],
      caso: 0,
      recibe: null,
      recibe_btc: null,
      montodolar: 0,
      montobtcenv: 0,
      dolar: 0,
      montobtc: 0,
      decWIF: null,
      tarifa: 0,
      fees: {
        nombre: ["Baja","Media","Alta","Por lote"],
        tipo:   ["hourFee","halfHourFee","fastestFee","fastestFee"],
        valor:  [0,0,0,0]
      },
      comision: 0,
      comis: 0,
      comisiontotal: 0,
      porcentaje: 0,
      opcionenvio: ["Dolar $","Bitcoin","Red Bitcoin"],
      formaenvio: 0,
      verifenviobtc: null,
      comision_address: null
    };
  },
  created () { 
    Security(1);
    this.Mostrar();
  },
  computed: {
  },
  methods: {
    Mostrar() {
      this.error =  null;
      this.wallet =  null;
      this.balance =  0;
      this.WIF =  null;
      this.Hex =  null;
      this.disabled =  true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.montodolar = 0;
      this.montobtcenv = 0;
      this.montobtc = 0;
      this.recibe = null;
      this.recibe_btc = null;
      this.movimientos= [];
      this.transactions = null;
      this.decWIF = null;
      this.comision = 0;
      this.comis = 0;
      this.comisiontotal = 0;
      this.porcentaje = 0;
      this.disponibilidad = true;
      this.verifenviobtc = null;
      this.comision_address=null;
      API({
        action:  'informacion',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password')
      }).then(response => {
        const user = response.data.user;
        this.wallet = user.wallet;
        this.WIF = secure.decrypt(user.wif, 'btcoin');
        this.Hex = secure.decrypt(user.hex, 'btcoin');
        this.saldo = parseFloat(response.data.saldo).toFixed(2);
        this.porcentaje = parseFloat(response.data.configuracion.price_spread);
        this.comision = parseFloat(response.data.configuracion.comision);
        this.comision_address=response.data.configuracion.comision_address;
        this.decWIF = response.data.configuracion.proveedor_wif;
        this.movimientos =  response.data.reporte;
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions = result.chain_stats['tx_count']+ result.mempool_stats['tx_count'];
          const received = result.chain_stats['funded_txo_sum'];
          const spent = result.chain_stats['spent_txo_sum'];
          this.balance = ((received-spent)/100000000).toFixed(8);
        });
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(2);
        });
        Bitcoin.privateKeyToAccount(secure.decrypt(response.data.configuracion.proveedor_wif, 'btcoin'));
        Bitcoin.getUtxo().then(result => {
          this.utxos = result;
        });
        Bitcoin.estimateFee().then(result => {
          this.fees.valor[0] = result.hourFee;
          this.fees.valor[1] = result.halfHourFee;
          this.fees.valor[2] = result.fastestFee;
          this.fees.valor[3] = result.fastestFee/4;
        });
      });
    },
    Enviar() {
      if (this.recibe==null) {
        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (this.montodolar==0) {
        this.$notify(Mensaje(3,'Debe ingresar un monto a enviar.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.recibe)){
        this.$notify(Mensaje(3,'El correo no es válido.'));
      } else {
      API({
        action:  'enviardinero',
        correo:   localStorage.getItem('Login'),
        clave:    localStorage.getItem('Password'),
        send: this.recibe,
        monto: this.montodolar
      }).then(response => {
        if (response.data.error){
          this.$notify(Mensaje(3,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        } else {
          this.$notify(Mensaje(2,response.data.message));
          this.saldo = 0;
          this.Mostrar();
        }   
      });
      }
    },
    Cambiar() {
      if (this.dolar==0 || this.dolar>this.saldo) {
        this.$notify(Mensaje(3,'Debe ingresar un monto a Intercambiar.'));
      } else {
       this.CalculoMercado();
        const tipofee = this.fees.tipo[this.tarifa];
        const resultfee = this.VerComision('fee');
        const comistotal = this.VerComision('total');
        const comist = this.VerComision('comision');
        const satoshi = parseFloat(resultfee) + parseFloat((100000000*(this.dolar/this.mercado)).toFixed(0)); 
        const fee_uso = parseFloat((100000000*(comist/this.mercado)).toFixed(0)); 
        const monto_total = parseFloat(this.dolar)+parseFloat(comistotal); 
        const price_spread = parseFloat(this.mercado*100/(100+this.porcentaje)).toFixed(2); 

        const to_value = [];
        to_value.push({to: this.wallet, value: satoshi});
        if (this.tarifa<3) {
          Bitcoin.sendTransactionComis(to_value,this.comision_address,fee_uso,tipofee).then(result => {
            API({
              action:   'intercambiar',
              correo:   localStorage.getItem('Login'),
              clave:    localStorage.getItem('Password'),
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   result.txid,
              mercado:  parseFloat(this.mercado).toFixed(2),
              monto_total:    monto_total,
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: price_spread,
              fee_uso_satoshi: fee_uso
            }).then(response => {
              if (response.data.error){
                this.$notify(Mensaje(3,response.data.message));
              } else {
                this.$notify(Mensaje(0,response.data.message));
              }   
            });
            this.saldo = 0;
            this.Mostrar();
          }).catch(err => {
            this.$notify(Mensaje(3,'Disculpe, Intente mas tarde.'));      
            //this.$notify(Mensaje(3,err));      
          });
        } else {
            API({
              action:   'intercambiar_lote',
              correo:   localStorage.getItem('Login'),
              clave:    localStorage.getItem('Password'),
              fees:     resultfee,
              satoshi:  satoshi,
              txhash:   result.txid,
              mercado:  this.mercado,
              monto_total: monto_total,
              origen:     Bitcoin.defaultAccount.address,
              destino:     this.wallet,
              monto_cambiar:    parseFloat(this.dolar),
              fee_uso: comist,
              price_spread: price_spread,
              fee_uso_satoshi: fee_uso
            }).then(response => {
              if (response.data.error){
                this.$notify(Mensaje(3,response.data.message));
              } else {
                this.$notify(Mensaje(0,response.data.message));
              }   
            });

        }
      } 
    },
    Caso(index) {
      if (this.caso == index) {
        this.caso = 0;
      } else {
        this.caso = index;
      }
    },
    getTxSize(vin, vout) {
      return vin * 180 + vout * 34 + 10 + vin;
    },
    VerComision(caso) {
      this.CalculoMercado();
      const value = (100000000*this.dolar/this.mercado).toFixed(0);
      const feePerByte = this.fees.valor[this.tarifa];
      this.error=feePerByte;
      let ninputs = 0;
      let availableSat = 0;
      for (let i = 0; i < this.utxos.length; i++) {
        if (this.utxos[i]['confirmed']) {
          availableSat += this.utxos[i].['satoshis'];
          ninputs++;
          if (availableSat >= value) break
        }
      }
      if (availableSat < value) {
          this.disponibilidad=false;
      } else {
        this.disponibilidad=true;
        const change = availableSat - value;
          const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
        const total = ((fee*this.mercado/100000000)+(this.dolar*this.comision/100)).toFixed(2);
        const comis = ((this.dolar*this.comision/100)).toFixed(2);
        if (this.dolar>0 && caso == 'total'){
          return total;
        } else if (this.dolar>0 && caso == 'comision'){
          return comis;
        } else if (this.dolar>0 && caso == 'fee'){
          return fee;
        } else {
          return 0;
        }   
      }
    },
    VerComisionEnv(caso) {
      this.CalculoMercado();
      const value = (100000000*this.montobtcenv/this.mercado).toFixed(0);
      const feePerByte = this.fees.valor[this.tarifa];
      let ninputs = 0;
      let availableSat = 0;
      for (let i = 0; i < this.utxos.length; i++) {
        if (this.utxos[i]['confirmed']) {
          availableSat += this.utxos[i].['satoshis'];
          ninputs++;
          if (availableSat >= value) break
        }
      }
      if (availableSat < value) {
          this.disponibilidad=false;
      } else {
        this.disponibilidad=true;
        const change = availableSat - value;
        const fee = this.getTxSize(ninputs, change > 0 ? 2 : 1) * feePerByte;
        const total = ((fee*this.mercado/100000000)+(this.montobtcenv*this.comision/100)).toFixed(2);
        const comis = ((this.montobtcenv*this.comision/100)).toFixed(2);
        if (this.montobtcenv>0 && caso == 'total'){
          return total;
        } else if (this.montobtcenv>0 && caso == 'comision'){
          return comis;
        } else if (this.montobtcenv>0 && caso == 'fee'){
          return fee;
        } else {
          return 0;
        }   
      }
    },
    CalculoMercado() {   
      Bitcoin.currentprice().then(result => {
          const btc = parseFloat((result.bpi.USD.rate).replace(',', ''));
          this.mercado  = ((btc*(100+this.porcentaje))/100).toFixed(2);
      });
    }

  }
};
</script>